import React, { Component } from "react";
import Display from "../Display/Display";
import "./Todo.css";

export default class Todo extends Component {
  state = {
    input: "",
    items: [],
  };

  onHandleChange = (event) => {
    this.setState({
      input: event.target.value,
    });
  };

  onFormSubmit = (event) => {
    event.preventDefault();
    const { input } = this.state;

    if (input !== "") {
      const allitems = this.state.items;
      allitems.push(input);
      this.setState({
        // items: [...this.state.items, input],
        items: allitems,
        input: "",
      });
      <Display items={this.state.items}/>
    }
    
  };

  clearAll = () => {
    const { items } = this.state;
    const delAll = items;

    delAll.splice(0, delAll.length);
    this.setState({
      items: delAll,
    });
  };

  onEdit = (key) => {
    let newvalue = window.prompt(
      "Enter the Todo list to update..",
      this.state.items[key]
    );
    const allitems = this.state.items;
    if (newvalue !== null) {
      allitems.splice(key, 1, newvalue);
    }
    this.setState({
      items: allitems,
    });
  };

  onDelete = (key) => {
    const resitems = this.state.items;
    resitems.splice(key, 1);
    this.setState({
      items: resitems,
    });
  };

  render() {
    const { input, items } = this.state;
    return (
      <div className="parent-container">
        <form
          className="container"
          onSubmit={this.onFormSubmit}
          autoComplete="off"
        >
          <h1>To-Do List</h1>
          <label htmlFor="input">
            <i className="fas fa-paperclip" title="Add Value to List"></i>
          </label>
          <input
            type="text"
            placeholder="Enter Items..."
            value={input}
            onChange={this.onHandleChange}
            id="input"
          />
        </form>
        <div className="display-container">
          <p onClick={this.clearAll} title="Delete all List Values">
            <u>Clear all </u>
          </p>
          <div className="list">
          <ul>
            {items.map((value, index) => (
              <li key={index}>
                <span>{value} </span>
                <i
                  className="fas fa-edit"
                  onClick={() => this.onEdit(index)}
                  title="Edit List Value"
                ></i>
                <i
                  className="fas fa-trash-alt"
                  onClick={() => this.onDelete(index)}
                  title="Delete List Value"
                ></i>
              </li>
            ))}
          </ul>
          </div>
        </div>
      </div>
    );
  }
}
