import React, { Component } from "react";
import "./Display.css"

export default class Display extends Component {
  render() {
    return (
      <div className="display-conatiner">
        <div className="display">
        <p>
          ToDo List App is a kind of app that generally used to maintain our
          day-to-day tasks or list everything that we have to do, with the most
          important tasks at the top of the list, and the least important tasks
          at the bottom. It is helpful in planning our daily schedules. We can
          add more tasks at any time and delete a task that is completed.
          <h4>Features:</h4>
          <p>
            In this version of the ToDo list, the user will be getting three
            options:
          </p>
          <ul>
            <li>
              Create (add) a new task or adding a new ToDo in the ToDo List App.
            </li>
            <li>
              See all the tasks or View all the ToDos that were added to the
              app.
            </li>
            <li>Delete any ToDo from the list of ToDos.</li>
          </ul>
        </p>
        </div>
      </div>
    );
  }
}
