import React from 'react'
import "./Headerr.css"
import { Link } from 'react-router-dom'

export default function Header() {

    return (
        <header>
            <Link to="/" className="link">Home</Link>
            <Link to="/todoapp" className="link">To-Do App</Link>
            <Link to="/display" className="link">About</Link>
        </header>
    )
}